"use client"

import { useEffect, useState } from "react"

export default function ResultsPage() {
  const [result, setResult] = useState<any>(null)

  useEffect(() => {
    const stored = localStorage.getItem("tender_result")
    if (stored) {
      setResult(JSON.parse(stored))
    }
  }, [])

  const downloadExcel = async () => {
    const result = localStorage.getItem("tender_result")
    if (!result) return

    const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE}/download_excel`, {
      method: "POST",
      body: result
    })

    const blob = await res.blob()
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement("a")
    link.href = url
    link.setAttribute("download", "analyzed_tender.xlsx")
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  return (
    <div className="min-h-screen bg-white dark:bg-gray-900 text-gray-900 dark:text-white px-4 py-10">
      <div className="max-w-4xl mx-auto space-y-6">
        <h1 className="text-3xl font-bold">üìä Tender Analysis Result</h1>

        {!result ? (
          <p className="text-gray-500 dark:text-gray-400">
            No result found. Please upload and analyze a tender first.
          </p>
        ) : (
          <>
            <div className="bg-gray-100 dark:bg-gray-800 p-6 rounded-xl shadow overflow-x-auto text-sm whitespace-pre-wrap">
              <pre>{JSON.stringify(result, null, 2)}</pre>
            </div>

            {/* ‚úÖ Download Excel Button */}
            <button
              onClick={downloadExcel}
              className="bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded"
            >
              ‚¨áÔ∏è Download as Excel
            </button>
          </>
        )}

        {/* Close Tab Button */}
        <button
          onClick={() => window.close()}
          className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded"
        >
          üîô Close Tab
        </button>
      </div>
    </div>
  )
}
